.user-modal-container {
    text-transform: capitalize;
    transition: 0.3s;
    top: 195px;
    box-shadow: 0px 0px 5px 1px rgba(128, 128, 128, 0.296);
    overflow: hidden;
    background-color: $clr2;
    position: absolute;
    height: min-content;
    width: 200px;
    border-radius: 20px;
    right: -10px;
    transform: translate(-50%, -50%);
    & ul {
        height: 100%;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    & li {
        padding-inline-start: 15px;
        padding-block-end: 5px;
        padding-block-start: 5px;
        &:not(:first-of-type) {
            font-weight: 300;
        }
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
            Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        cursor: pointer;
        &:hover {
            background: $clr4;
        }
    }
    .user-modal-screen {
        position: fixed;
        opacity: 30%;
        height: 100vh;
        width: 100vw;
    }
    & .user-modal-span {
        color: $clr3;
        width: 100%;
        height: 100%;
        display: inline-block;
        &:hover {
            background-color: $clr4;
        }
    }
    & hr {
        border-width: 0.1px;
        opacity: 30%;
    }
    .user-notifications-dot {
        background-color: $clr1;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        height: 25px;
        width: 25px;
        padding-inline-end: 2px;
        padding-block-end: 2px;
        border-radius: 50%;
        color: $clr2;
        right: 8%;
        top: 6%;
        border: 1px solid $clr3;
    }
}

.detail-layout .user-modal-container {
    left: 73.1%;
}

.user-modal-about-link {
    font-weight: bold;
}
